<template>
	<div class="Profit-function">
		<div class="function-box">
			<div>
				<p class="profit-title">销售量</p>
				<p class="profit-value">{{saleNum}}</p>
			</div>
			<div>
				<p class="profit-title">销售额</p>
				<p class="profit-value">￥{{saleAllMoney}}</p>
			</div>
			<div>
				<p class="profit-title">毛利</p>
				<p class="profit-value"></p>
			</div>
			<div >
				<p class="profit-title">毛利率</p>
				<p class="profit-value">81.5%</p>
			</div>
		</div>
		<div class="function-box">
			<div v-for="(item,index) in profitArrTitle" :key="index" class="profit-btns" @click="profitClick(index)">
				{{item}}
			</div>
		</div>
	</div>
</template>

<script>



export default {
  name: 'ProfitFunctionNav',
  data(){
	return{
		profitArrTitle:[
			'数量',
			'折后金额',
			'毛利润',
			'其他',
		],
		todayOrderList:{
			list:[],
			allMoney:0,
			allNum:0,
		},
	}
  },
  methods:{
	profitClick(index){
		this.$emit("profitClick",index)
	},
  },
  props:{
	profitObj:{
		type:Array,
		default(){
			return []
		}
	}
  },
  computed:{
	//销售量
	saleAllMoney(){
		let res=0
		this.profitObj.forEach(obj=>{
			if(obj.productList.length<=0) return res
			obj.productList.forEach(pro=>{
				res+=pro.productnum*pro.price
			})
		})
		return res.toFixed(2)
	},
	saleNum(){
		let res=0
		this.profitObj.forEach(obj=>{
			if(obj.productList.length<=0) return res
			obj.productList.forEach(pro=>{
				res+=parseInt(pro.productnum)
			})
		})
		return res
	}
  },
  watch:{
	todayList(newVal){
		//todayOrderList赋值
		this.setObjAsArr('todayOrderList',newVal);
	}
  }
}
</script >

