<template>
	<div class="btns-date">
		<ul class="profit-date-box">
			<li v-for="(item,index) in textArr" :key="index" :class="{profileaction:currentIndex==index}" @click="itemClick(index)">{{item}}</li>
		</ul>
	</div>
</template>

<script>



export default {
  name: 'DateBtnsNav',
  data(){
	return{
		currentIndex:0
	}
  },
  props:{
	textArr:{
		type:Array,
		default(){
			return [
				'今日',
				'昨日',
				'近七天',
				'近30天',
			]
		}
	}
  },
  methods:{
	itemClick(index){
		if(this.currentIndex===index)return
		this.currentIndex=index
		//把选中的按钮传出去
		this.$emit("profitDateClick",this.currentIndex)
	}
  }
}
</script >

<style spoced>
.btns-date{
	padding:5px;
	position:relative;
}
ul,li{
	list-style:none;
}
.profit-date-box{
	background:#fff;
	display:flex;
	border-radius:4px;
}
.profit-date-box li{
	flex:1;
	text-align:center;
	border-radius:6px;
	color:black;
	font-size:13px;
	line-height:30px;
}
.profit-date-box .profileaction{
	background:#ff5959;
	color:white;
}

</style>
